.cbs-cart {
  color: white;
  background: var(--color_brown);
  padding: 1em;
  display: flex;
  flex-direction: column;
  width: 15%;
  min-width: 15%;
  min-height: 85vh;
  height: auto;
  justify-content: flex-start;
  /* margin-top: 7.1vh;  */
}

.cbs-cart-add-button {
  margin-left: 2vh;
  cursor: pointer;
}
.cbs-cart-toggle-button {
  background: none;
  outline: none;
  border: none;
  font-weight: 600;
  cursor: pointer;
  align-self: self-start;
  padding: 0;
  color: white;
  align-self: flex-end;
}
.cbs-cart-closed {
  padding: 2vh 2vw;
  width: 15%;
}
.cbs-cart-closed > button {
  color: black;
}

.item-cart > div {
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
}

.cart-price {
  margin-left: 1vw;
}
.cart-quantite {
  margin-left: 1vw;
}
h3 {
  border-top: 1px solid #ddd;
  margin: 1em;
  padding-top: 1vh;
}
.vider-panier-cta {
  margin-left: auto;
  display: block;
  margin-bottom: 1vh;
}

/* ----------- Responsive ----------- */
/* Smartphones */
@media (max-width: 480px) {
  .cbs-cart {
    width: 100%;
    height: 40vh; /* Hauteur fixe */
    max-height: 50vh; /* Hauteur maximale */
    min-height: 30vh; /* Hauteur minimale */
    overflow-y: auto; /* Scroll si le contenu dépasse */
  }
}
