.cbs-shopping-list {
  flex: 1;
  margin-bottom: 10vh;
}

.cbs-coffee-list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10vh 4vw;
  justify-content: center;
}
.cbs-coffee-list li {
  width: 23vw;
  height: auto;
  box-sizing: border-box;
}
.cbs-description-pack {
  margin: 1vh 1vw;
  overflow-wrap: break-word;
  text-align: justify;
}
.addCartCTA {
  background-color: var(--color_blue);
  border: none;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 30%;
  color: whitesmoke;
  font-weight: bold;
  margin: 1vh auto;
  padding: 1vh 0.5vw;
  letter-spacing: 0.1em;
}
.addCartCTA:hover {
  opacity: 0.7;
}

/* ----------- Responsive ----------- */
/* Tablettes */
@media (max-width: 1200px) {
  .cbs-coffee-list li {
    width: 30vw;
    height: auto;
    min-height: 40vh;
    max-width: 100vw;
  }
  .cbs-description-pack,
  .cbs-coffee-item-name {
    word-break: break-word;
    overflow-wrap: break-word;
  }
}
/* Smartphones */
@media (max-width: 480px) {
  .cbs-coffee-list {
    flex-direction: column;
    gap: 4vh 0;
    align-items: center;
  }
  .cbs-coffee-list li {
    width: 90vw;
    height: auto;
    min-height: 40vh;
    max-width: 100vw;
  }
}
